import { useEffect, useState } from "react";
import List from "../Components/List";
import axios from "axios";
import NavBar from "../Components/NavBar";

interface Item {
  uuid: string;
  name: string;
  produce: string;
  expire: number;
  type: string;
}

function request(items: Item[]) {
  axios
    .post("https://www.rnadint.com/items", items, {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
        user: localStorage.getItem("user"),
      },
    })
    .then(() => console.log("success."))
    .catch((error) => console.log(error));
}

function daysDifference(d0: Date, d1: Date) {
  var diff = new Date(d0).valueOf() - new Date(d1).valueOf();
  return Math.round(diff / 8.64e7);
}

const expireDays = (item: Item): number => {
  const today = new Date();
  let result = new Date(item.produce);
  result.setDate(result.getDate() + item.expire);

  return daysDifference(result, today) + 1;
};

const ListPage = () => {
  const [items, setItems] = useState<Item[]>([]);

  useEffect(() => {
    axios
      .get<Item[]>("https://www.rnadint.com/items", {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
          user: localStorage.getItem("user"),
        },
      })
      .then((res) => setItems(res.data));
  }, []);

  return (
    <>
      <NavBar curr_page="list" />
      <List
        items={items}
        onDelete={(uuid) => {
          setItems(items.filter((e) => e.uuid !== uuid));
          request(items.filter((e) => e.uuid !== uuid));
        }}
      />
    </>
  );
};

export default ListPage;
