import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./NavBar.css";

interface Prop {
  curr_page: string;
}

const NavBar = ({ curr_page }: Prop) => {
  return (
    <Navbar bg="primary" data-bs-theme="dark">
      <Container>
        <Navbar.Brand>
          <strong>导航栏</strong>
        </Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link>
            <Link to="/">
              {curr_page == "list" ? <strong>列表</strong> : "列表"}
            </Link>
          </Nav.Link>
          <Nav.Link>
            <Link to="/form">
              {curr_page == "form" ? <strong>添加</strong> : "添加"}
            </Link>
          </Nav.Link>
          <Nav.Link>
            <Link to="/user">
              {curr_page == "user" ? <strong>用户</strong> : "用户"}
            </Link>
          </Nav.Link>
        </Nav>
      </Container>
    </Navbar>
  );
};

export default NavBar;
