import Dropdown from "react-bootstrap/Dropdown";

import { DropdownButton } from "react-bootstrap";
import { useEffect, useState } from "react";

interface Item {
  uuid: string;
  name: string;
  produce: string;
  expire: number;
  type: string;
}

interface Props {
  items: Item[];
  onSelect: (type: string) => void;
  curr_text: string;
}

const DropdownButtonList = ({ items, onSelect, curr_text }: Props) => {
  let temp: string[] = [];
  for (let index = 0; index < items.length; index++) {
    temp.push(items[index].type);
  }

  temp = temp.filter((value, index) => temp.indexOf(value) === index);

  return (
    <>
      <DropdownButton id="dropdown-basic-button" title={curr_text}>
        <Dropdown.Item key="all" onClick={() => onSelect("all")}>
          全部
        </Dropdown.Item>
        {temp.map((type) => (
          <Dropdown.Item key={type} onClick={() => onSelect(type)}>
            {type}
          </Dropdown.Item>
        ))}
      </DropdownButton>
    </>
  );
};

export default DropdownButtonList;
