import React, { useState } from "react";
import NavBar from "../Components/NavBar";

const UserPage = () => {
  const [value, setValue] = useState(localStorage.getItem("user") || "");
  function handle() {
    alert(value);
  }
  return (
    <>
      <NavBar curr_page="user" />
      <div className="mb-3" style={{ padding: "15px", fontWeight: "bold" }}>
        <label htmlFor="type" className="form-label">
          当前用户:
        </label>

        <div className="input-group" style={{ width: "50vh" }}>
          <input
            type="text"
            id="user-box"
            className="form-control"
            value={value}
            onChange={(e) => {
              setValue(e.target.value);
            }}
          />
          <button
            className="btn btn-primary"
            type="button"
            id="button-addon1"
            data-mdb-ripple-color="dark"
            onClick={() => localStorage.setItem("user", value)}
          >
            更改
          </button>
        </div>
      </div>
    </>
  );
};

export default UserPage;
