import { LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import axios from "axios";
import { useState, useEffect } from "react";
import Form from "../Components/Form";
import { v4 as uuid } from "uuid";
import NavBar from "../Components/NavBar";

interface Item {
  uuid: string;
  name: string;
  produce: string;
  expire: number;
  type: string;
}

function request(items: Item[]) {
  axios
    .post("https://www.rnadint.com/items", items, {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
        user: localStorage.getItem("user"),
      },
    })
    .then(() => console.log("success."))
    .catch((error) => console.log(error));
}

const FormPage = () => {
  const [items, setItems] = useState<Item[]>([]);
  useEffect(() => {
    axios
      .get<Item[]>("https://www.rnadint.com/items", {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
          user: localStorage.getItem("user"),
        },
      })
      .then((res) => setItems(res.data));
  }, []);

  return (
    <>
      <NavBar curr_page="form" />
      <LocalizationProvider dateAdapter={AdapterDayjs}>
        <div className="mb-5">
          <Form
            onSubmit={(itemForm) => {
              setItems([...items, { ...itemForm, uuid: uuid() }]);
              let temp = [...items, { ...itemForm, uuid: uuid() }];
              temp = temp.sort((i1, i2) => {
                if (i1.expire > i2.expire) {
                  return 1;
                }

                if (i1.expire < i2.expire) {
                  return -1;
                }

                return 0;
              });
              request(temp);
            }}
          />
        </div>
      </LocalizationProvider>
    </>
  );
};

export default FormPage;
