import "./List.css";
import DropdownButtonList from "./DropdownButtonList";
import { useState } from "react";
import { pinyin } from "pinyin-pro";
import { Button } from "react-bootstrap";
import { setServers } from "dns";

interface Item {
  uuid: string;
  name: string;
  produce: string;
  expire: number;
  type: string;
}

interface Props {
  items: Item[];
  onDelete: (uuid: string) => void;
}

function daysDifference(d0: Date, d1: Date) {
  var diff = new Date(d0).valueOf() - new Date(d1).valueOf();
  return Math.round(diff / 8.64e7);
}

const expireDays = (item: Item): number => {
  const today = new Date();
  let result = new Date(item.produce);
  result.setDate(result.getDate() + item.expire);

  return daysDifference(result, today) + 1;
};

function dateToString(date: Date) {
  let month = "";
  if (date.getMonth() + 1 < 10) {
    month = "0" + (date.getMonth() + 1).toString();
  } else {
    month = (date.getMonth() + 1).toString();
  }

  let day = "";
  if (date.getDate() < 10) {
    day = "0" + date.getDate().toString();
  } else {
    day = date.getDate().toString();
  }

  let year = date.getFullYear();

  return `${year.toString()}年${month}月${day}日`;
}

const List = ({ items, onDelete }: Props) => {
  const [sortMethodName, setSortMethodName] = useState("↓");
  const [sortMethodTime, setSortMethodTime] = useState("↑");
  const [currType, setCurrType] = useState("all");
  const [search, setSearch] = useState("");

  const bestBefore = (item: Item) => {
    let result = new Date(item.produce);

    result.setDate(result.getDate() + item.expire);
    return dateToString(result);
  };

  if (items.length === 0) {
    return null;
  }

  return (
    <>
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>
              名称
              <Button
                className="btn btn-light"
                style={{ marginLeft: "10px" }}
                onClick={() => {
                  if (sortMethodName != "↓") {
                    items = items.sort((n1, n2) => {
                      if (
                        pinyin(n1.name)[0].charCodeAt(0) ==
                        pinyin(n2.name)[0].charCodeAt(0)
                      ) {
                        return 0;
                      } else {
                        return pinyin(n1.name)[0].charCodeAt(0) >
                          pinyin(n2.name)[0].charCodeAt(0)
                          ? 1
                          : -1;
                      }
                    });
                    setSortMethodName("↓");
                  } else {
                    items = items.sort((n1, n2) => {
                      if (
                        pinyin(n1.name)[0].charCodeAt(0) ==
                        pinyin(n2.name)[0].charCodeAt(0)
                      ) {
                        return 0;
                      } else {
                        return pinyin(n1.name)[0].charCodeAt(0) >
                          pinyin(n2.name)[0].charCodeAt(0)
                          ? -1
                          : 1;
                      }
                    });
                    setSortMethodName("↑");
                  }
                }}
              >
                {sortMethodName}
              </Button>
            </th>
            <th>
              <DropdownButtonList
                items={items}
                onSelect={setCurrType}
                curr_text={currType == "all" ? "全部" : currType}
              ></DropdownButtonList>
            </th>
            <th>生产日期</th>
            <th>最佳食用日期</th>
            <th>
              剩余天数
              <Button
                className="btn btn-light"
                style={{ marginLeft: "10px" }}
                onClick={() => {
                  if (sortMethodTime != "↓") {
                    items = items.sort((n1, n2) => {
                      if (
                        parseInt(bestBefore(n1)) == parseInt(bestBefore(n2))
                      ) {
                        return 0;
                      } else {
                        return parseInt(bestBefore(n1)) >
                          parseInt(bestBefore(n2))
                          ? 1
                          : -1;
                      }
                    });
                    setSortMethodTime("↓");
                  } else {
                    items = items.sort((n1, n2) => {
                      if (
                        parseInt(bestBefore(n1)) == parseInt(bestBefore(n2))
                      ) {
                        return 0;
                      } else {
                        return parseInt(bestBefore(n1)) >
                          parseInt(bestBefore(n2))
                          ? -1
                          : 1;
                      }
                    });
                    setSortMethodTime("↑");
                  }
                }}
              >
                {sortMethodTime}
              </Button>
            </th>
            <th>
              搜索：
              <input
                onChange={(e) => {
                  setSearch(e.target.value);
                }}
              ></input>
            </th>
          </tr>
        </thead>
        <tbody>
          {items
            .filter(
              (item) =>
                item.type == currType ||
                (currType == "all" &&
                  (item.name.indexOf(search) != -1 || search == ""))
            )
            .map((item) => (
              <tr key={item.uuid}>
                <td className="name">{item.name}</td>
                <td className="text">{item.type}</td>
                <td className="date">{dateToString(new Date(item.produce))}</td>
                <td className="date">{bestBefore(item)}</td>
                <td
                  className="number"
                  style={{
                    color: `${expireDays(item) < 0 ? "red" : ""}`,
                  }}
                >
                  {expireDays(item)} 天
                </td>

                <td>
                  <button
                    className="btn btn-outline-danger"
                    onClick={() => onDelete(item.uuid)}
                  >
                    删除
                  </button>
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </>
  );
};

export default List;
